import './profile.scss'
import { Link, Outlet, Route, Routes, useNavigate } from 'react-router-dom'
import { useEffect, useRef } from 'react'

function Profile() {
    let navigate = useNavigate()
    useEffect(() => {
        if (!localStorage.getItem("token")) {
            navigate('/')
        }
    }, [window.location.href])

    useEffect(() => {
        if (!localStorage.getItem("token")) {
            navigate('/')
        }
    }, [localStorage.getItem("token")])
    

    let line1 = useRef(null)
    let line2 = useRef(null)
    let line3 = useRef(null)
    let line4 = useRef(null)
    let line5 = useRef(null)

    function handleLine1() {
        line1.current.classList.add("active")
        line2.current.classList.remove("active")
        line3.current.classList.remove("active")
        line4.current.classList.remove("active")
        line5.current.classList.remove("active")
    }
    function handleLine2() {
        line2.current.classList.add("active")
        line1.current.classList.remove("active")
        line3.current.classList.remove("active")
        line4.current.classList.remove("active")
        line1.current.classList.remove("activecha")
        line5.current.classList.remove("active")
    }
    function handleLine3() {
        line3.current.classList.add("active")
        line1.current.classList.remove("activecha")
        line2.current.classList.remove("active")
        line1.current.classList.remove("active")
        line4.current.classList.remove("active")
        line5.current.classList.remove("active")
    }
    function handleLine4() {
        line4.current.classList.add("active")
        line2.current.classList.remove("active")
        line1.current.classList.remove("activecha")
        line3.current.classList.remove("active")
        line1.current.classList.remove("active")
        line5.current.classList.remove("active")
    }
    function handleLine5() {
        line5.current.classList.add("active")
        line2.current.classList.remove("active")
        line3.current.classList.remove("active")
        line4.current.classList.remove("active")
        line1.current.classList.remove("activecha")
        line1.current.classList.remove("active")
    }

    return (
        <>
            <div className='auth__top boxdel'>
                <div className='single__top-wrapper'>
                    <div className='single__top-left'>
                        <select className='single__top-category'>
                            <option value="1"> Любая категория</option>
                        </select>
                        <input className='single__top-search' placeholder='Что будем искать ?' />
                        <select className='single__top-location'>
                            <option value="2">
                                По всей Узбекистану
                            </option>
                        </select>
                        <button className='single__top-btn'>
                            Найти
                        </button>
                    </div>
                    <div className='single__top-right'>
                        <span className='single__top-plus'>+</span>
                        Добавить обьявления
                    </div>
                </div>
            </div>
            <div className='balance__wrapper'>
                <div className='balance__inner-wrapper'>
                    <h3 className='balance__name'>
                        Здравствуйте Mirmuhsin Mirahmatov
                    </h3>
                    <div className='balance__right-wrapper'>
                        <p className='balance__nums'>
                            Ваш счет: 2 3000 сум
                        </p>
                        <button className='balance__right-btn'>
                            <span className='balance__right-plus'>+</span>Пополнить
                        </button>
                    </div>
                </div>
                <span className='balance__line'></span>
                <ul className='balance__list'>
                    <li className='balance__item '>
                        <Link onClick={handleLine1} ref={line1} to={'/profile'} className='balance__link activecha'>
                            Объявления
                        </Link>
                    </li>
                    <li className='balance__item'>
                        <Link onClick={handleLine2} ref={line2} to={'messages'} className='balance__link'>
                            Сообщения
                        </Link>
                    </li>
                    <li className='balance__item'>
                        <Link onClick={handleLine3} ref={line3} to={'payment'} className='balance__link'>
                            Платежи и счёт
                        </Link>
                    </li>
                    <li className='balance__item'>
                        <Link onClick={handleLine4} ref={line4} to={'settings'} className='balance__link'>
                            Настройки
                        </Link>
                    </li>
                    <li className='balance__item'>
                        <Link onClick={handleLine5} ref={line5} to={'myBusiness'} className='balance__link'>
                            Мой бизнес
                        </Link>
                    </li>
                </ul>
                <span className='balance__line width-reg'></span>
            </div>
            <Outlet/>
        </>
    )
}

export default Profile