import './header.scss'
import logo from './../../assets/images/logo.svg'
import { Link, useNavigate } from 'react-router-dom'
import { useEffect, useRef, useState } from 'react'
import ava from './../../assets/images/ava.png'
import { useTranslation } from 'react-i18next'
import { changeLanguage } from 'i18next'


function Header() {
    let without = useRef(null)
    let withtoken = useRef(null)
    let { t, i18n } = useTranslation()
    let [lang, setLang] = useState()
    let uz = useRef(null)
    let ru = useRef(null)
    let nav1 = useRef(null)
    let nav2 = useRef(null)
    let navigate = useNavigate()

    useEffect(() => {
        if (window.location.href == 'https://mirmuhsin-kivi.netlify.app/products') {
            nav1.current.classList.add("active3")
            nav2.current.classList.remove("active3")
        } else if (window.location.href == 'https://mirmuhsin-kivi.netlify.app/add-product') {
            nav2.current.classList.add("active3")
            nav1.current.classList.remove("active3")
        }else{
            nav2.current.classList.remove("active3")
            nav1.current.classList.remove("active3")
        }
    }, [window.location.href])



    useEffect(() => {
        i18n.changeLanguage(localStorage.getItem("lang"))
        if (localStorage.getItem("lang") == 'uz') {
            uz.current.classList.add("active-lang")
        } else {
            ru.current.classList.add("active-lang")
        }
    }, [])

    function handleLanguageUz(e) {
        setLang("uz")
        i18n.changeLanguage("uz")
        e.target.classList.add("active-lang")
        ru.current.classList.remove("active-lang")
        localStorage.setItem("lang", "uz")
    }
    function handleLanguageRu(e) {
        setLang("ru")
        i18n.changeLanguage("ru")
        e.target.classList.add("active-lang")
        uz.current.classList.remove("active-lang")
        localStorage.setItem("lang", "ru")
    }

    useEffect(() => {
        if (localStorage.getItem("token")) {
            without.current.style.display = 'none'
            withtoken.current.style.display = 'flex'
        } else {
            without.current.style.display = 'flex'
            withtoken.current.style.display = 'none'
        }
    }, [localStorage.getItem("token")])
    function logOut() {
        localStorage.removeItem("token")
        navigate('/login')
    }
    return (
        <>
            <div className='header__container'>
                <div className='header__inner-wrapper'>
                    <Link className='header__logo-wrapper' to="/">
                        <img className='header__logo-img' src={logo} alt="logo" />
                        <p className='header__logo-text'>
                            {t("headerLog")}
                        </p>
                    </Link>
                    <ul className='header__navbar'>
                        <li className='header__navbar-item'>
                            <Link ref={nav1} to={'./products'} className='header__link'>
                                {t("nav1")}
                            </Link>
                        </li>
                        <li className='header__navbar-item'>
                            <Link ref={nav2} to={'/add-product'} className='header__link'>
                                {t("nav2")}
                            </Link>
                        </li>
                        <li className='header__navbar-item'>
                            <Link className='header__link'>
                                {t("nav3")}
                            </Link>
                        </li>
                        <li className='header__navbar-item'>
                            <Link className='header__link'>
                                {t("nav4")}
                            </Link>
                        </li>
                    </ul>
                    <div className='header__lang-wrapper'>
                        <p onClick={handleLanguageRu} ref={ru} className='header__lang-item'>
                            Рус
                        </p>
                        <span className='header__lang-line'></span>
                        <p onClick={handleLanguageUz} ref={uz} className='header__lang-item'>
                            O’z
                        </p>
                    </div>
                    <div className='header__profile-wrapper'>
                        <div ref={without} className='without'>
                            <Link to={'/login'} className='header__profile-item'>
                                {t("log")}
                            </Link>
                            <span className='header__prof-line'></span>
                            <Link className='header__profile-item'>
                                {t("regist")}
                            </Link>
                        </div>
                        <Link to={'/profile'} className="header__profile with" style={{ display: 'flex', alignItems: 'center', cursor: 'pointer', gap: '13px' }} ref={withtoken}>
                            Mirmuhsin
                            <img src={ava} alt="ava" />
                            <div className='logOut'>
                                <button className='logout__btn' onClick={logOut}>Log out</button>
                            </div>
                        </Link>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Header