import './product-list.scss'
import { useState, useRef } from 'react'
import ProductCard from '../../components/productCard'
import { Link } from 'react-router-dom'

function ProductList() {
    let moreWrapper = useRef(null)
    let fullWrapper = useRef(null)
    let shortWrapper = useRef(null)
    let [product, setProduct] = useState([])
    fetch('https://fakestoreapi.com/products')
        .then(res => res.json())
        .then(data => setProduct(data))
    function handleShow() {
        shortWrapper.current.innerHTML = null
        fullWrapper.current.style.display = "flex"
        moreWrapper.current.remove()
    }
    return (
        <>
            <ul>
                <div className='product-list' ref={shortWrapper}>
                    {
                        product?.slice(0, 16).map(item => (
                            <Link key={item.id} style={{textDecoration: "none"}} to={`/product/${item.id}`}>
                                <ProductCard
                                    key={item.id}
                                    pic={item.image}
                                    title={item.title}
                                    price={item.price}
                                />
                            </Link>
                        ))
                    }
                </div>
                <div ref={fullWrapper} style={{ display: "none" }} className='product-list'>
                    {
                        product?.map(item => (
                            <Link key={item.id} style={{textDecoration: "none"}} to={`/product/${item.id}`}>
                                <ProductCard
                                    key={item.id}
                                    pic={item.image}
                                    title={item.title}
                                    price={item.price}
                                />
                            </Link>
                        ))
                    }
                </div>
            </ul>
            <div className='more__button wrapper' ref={moreWrapper}>
                <span className='product__line'></span>
                <p className='more__button-text' onClick={handleShow}>Покозать еще</p>
            </div>
        </>
    )
}

export default ProductList