import './singleProduct.scss'
import arrowDown from './../../assets/images/arrow-down.svg'
import { Link, Outlet, useParams } from 'react-router-dom'
import ProductCard from '../../components/productCard'
import { useEffect, useRef, useState } from 'react'

function SingleProduct() {
    let hidden1 = useRef(null)
    let hidden2 = useRef(null)
    let [single, setSingle] = useState([])
    let { id } = useParams();

    useEffect(() => {
        fetch(`https://fakestoreapi.com/products/${id}`)
            .then(res => res.json())
            .then(sing => setSingle(sing))
    }, [id])

    let line1 = useRef(null)
    let line2 = useRef(null)

    function handleLine1() {
        line1.current.classList.add("active2")
        line2.current.classList.remove("active2")
    }
    function handleLine2() {
        line1.current.classList.remove("active2")
        line2.current.classList.add("active2")
    }

    return (
        <>
            <div className='single__container'>
            <div className='single__top'>
                <div className='single__top-wrapper'>
                    <div className='single__top-left'>
                        <select className='single__top-category'>
                            <option value="1"> Любая категория</option>
                        </select>
                        <input className='single__top-search' placeholder='Что будем искать ?' />
                        <select className='single__top-location'>
                            <option value="2">
                                По всей Узбекистану
                            </option>
                        </select>
                        <button className='single__top-btn'>
                            Найти
                        </button>
                    </div>
                    <div className='single__top-right'>
                        <span className='single__top-plus'>+</span>
                        Добавить обьявления
                    </div>
                </div>
            </div>

                <div className='single__middle container'>
                    <p className='single__breadcumb'>
                        Объявления   /   Ташкент Недвижимость   /   Ташкент Квартиры   /   Ташкент Продажа   /   Новостройки Ташкент
                    </p>
                    <h2 className='single__heading'>
                        {single.title}
                    </h2>
                    <div className='single__middle-wrapper'>
                        <div className='single__middle-images'>
                            <div className='single__images-left'>
                                <img className='single__left-img' src={single.image} alt="image" />
                                <img className='single__left-img' src={single.image} alt="image" />
                                <img className='single__left-img' src={single.image} alt="image" />
                                <img className='single__left-img' src={single.image} alt="image" />
                                <img className='single__left-img' src={single.image} alt="image" />
                            </div>
                            <div className='single__images-right'>
                                <img className='single__right-img' src={single.image} alt="img" />
                            </div>
                        </div>
                        <div className='single__middle-text-wrapper'>
                            <div className='single__middle-articul-wrapper'>
                                <p className='single__middle-articul'>
                                    Артикул: 4418970
                                </p>
                                <p className='single__middle-time'>
                                    обновлено: 28.04.17 добавлено: 28.04.17
                                </p>
                            </div>
                            <h3 className='single__middle-price'>
                                {single.price} y.e.
                            </h3>
                            <p className='single__middle-location'>
                                Ташкент, Ташкентская область,  Юнусабадский район
                            </p>
                            <div className='single__middle-phone-wrapper'>
                                <p className='single__middle-phone'>99891 166 <span ref={hidden1}>****</span></p>
                                <button onClick={()=>hidden1.current.textContent = 7777} className='single__phone-btn'>Покозать польностю</button>
                            </div>
                            <div className='single__middle-phone-wrapper'>
                                <p className='single__middle-phone'>99891 166 <span ref={hidden2}>****</span></p>
                                <button onClick={()=>hidden2.current.textContent = 8888} className='single__phone-btn'>Покозать польностю</button>
                            </div>
                            <p className='single__middle-author'>
                                Автор обьявлении: <span className='single__author'>Зухриддин Темиров</span>
                            </p>
                            <div className='single__middle-btns-wrapper'>
                                <button className='single__btn-middle'>
                                    Написать автору
                                </button>
                                <button className='single__btn-middle'>
                                    Предложить свою цену
                                </button>
                            </div>
                            <div className='single__middle-table'>
                                <div className='single__middle-tabel-item t single__middle-table1'>
                                    <p className='single__middle-table-heading'>
                                        Количество комнат:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        6
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item t single__middle-table2'>
                                    <p className='single__middle-table-heading'>
                                        Общая площадь:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        40 м2
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item t r single__middle-table3'>
                                    <p className='single__middle-table-heading'>
                                        Этаж:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        32
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item single__middle-table4'>
                                    <p className='single__middle-table-heading'>
                                        Этажность дома:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        6
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item single__middle-table5'>
                                    <p className='single__middle-table-heading'>
                                        Состояние квартиры:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        Первая сдача
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item r single__middle-table6'>
                                    <p className='single__middle-table-heading'>
                                        Этаж:
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        32
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item single__middle-table7'>
                                    <p className='single__middle-table-heading'>
                                        Планировка
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        <span className='single__middle-table-link'>
                                            Планировка
                                        </span>
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item single__middle-table8'>
                                    <p className='single__middle-table-heading'>
                                        Год постройки/сдачи
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        <span className='single__middle-table-link'>
                                            Сдача в 2017
                                        </span>
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item r single__middle-table9'>
                                    <p className='single__middle-table-heading'>
                                        Ремонт
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        Авторский проект
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item b single__middle-table10'>
                                    <p className='single__middle-table-heading'>
                                        Санузел
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        <span className='single__middle-table-link'>
                                            2 санузла и более
                                        </span>
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item b single__middle-table11'>
                                    <p className='single__middle-table-heading'>
                                        Меблирована
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        <span className='single__middle-table-link'>
                                            Да
                                        </span>
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item r b single__middle-table12'>
                                    <p className='single__middle-table-heading'>
                                        Высота потолков
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        32
                                    </p>
                                </div>
                                <div className='single__middle-tabel-item-big  single__middle-table13'>
                                    <p className='single__middle-table-heading '>
                                        Рядом есть
                                    </p>
                                    <p className='single__middle-table-subtitle'>
                                        <span className='single__middle-table-link'>
                                            Больница, поликлиника, Детская, площадка, Детский сад, Остановки, Парк, зелёная зона, Развлекательные заведения, Рестораны, кафе
                                        </span>
                                    </p>
                                </div>
                                <div className='single__middle-feedback'>
                                    <p className='single__middle-feedback-text'>
                                        Срочно продаётся 3х ком на 1 этоже 7 этажном кирпичном доме. Новостройка Голден Хаус жылой комплекс Етти Чинор Очень качественный и дорогой ремонт. Комнаты полнастю разделные болшые. 2 сан узла. Для лишних вещей есть кладовка.Ремонт шыкарный обсалютно новый. <br /><br />

                                        Ремонт делолся из очень дорогова материяла. Дорогая класическая кухонная мебель. Джаккузи,Тёплые полы в каждой комнате. Дорогие люстры вовсех комнатах решодки на окнах. Теретория охранается. Всё предусмотренно для детей деские площядки качели итд. Торг наместе. Торопитесь продаю нужны денги. Первый золотой этаж и паследний в этом коплексе. Торопитесь.
                                    </p>
                                    <div className='single__middle-feedback-btn-wrapper'>
                                        <p className='single__middle-feedback-watches'>
                                            Просмотры: 10958
                                        </p>

                                        <p className='single__middle-feedback-report'>
                                            Пожаловатся
                                        </p>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='single__middle-same-wrapper'>
                        <div className='single__middle-same'>
                            <Link ref={line1} onClick={handleLine1} to={`/product/${id}`} className='single__middle-nav-link active2'>Похожие объявления</Link>
                            <Link ref={line2} onClick={handleLine2} to={'author'} className='single__middle-nav-link'>Обьявление автора</Link>
                            <span className='single__line'></span>
                        </div>
                        <Outlet/>
                    </div>
                </div>
            </div>
        </>
    )
}

export default SingleProduct