import './home.scss'
import icon1 from './../../assets/images/plane.svg'
import icon2 from './../../assets/images/shop.svg'
import icon3 from './../../assets/images/elec.svg'
import icon4 from './../../assets/images/work.svg'
import icon5 from './../../assets/images/gift.svg'
import icon6 from './../../assets/images/mount.svg'
import icon7 from './../../assets/images/building.svg'
import icon8 from './../../assets/images/game.svg'
import icon9 from './../../assets/images/business.svg'
import { Link, NavLink, Route, Routes, useNavigate } from 'react-router-dom'
import Slider from '../../components/slider'
import ProductList from '../../components/product-list'
import WatchedList from '../../components/watched-list'
import { useRef } from 'react'


function Home() {
    let line1 = useRef(null)
    let line2 = useRef(null)
    let line3 = useRef(null)
    let line4 = useRef(null)
    let line5 = useRef(null)

    function handleLine1() {
        line1.current.classList.add("active")
        line2.current.classList.remove("active")
        line3.current.classList.remove("active")
    }
    function handleLine2() {
        line1.current.classList.remove("active")
        line2.current.classList.add("active")
        line3.current.classList.remove("active")
    }
    function handleLine3() {
        line1.current.classList.remove("active")
        line2.current.classList.remove("active")
        line3.current.classList.add("active")
    }
    function handleLine4() {
        line5.current.classList.remove("active")
        line4.current.classList.add("active")
    }
    function handleLine5() {
        line4.current.classList.remove("active")
        line5.current.classList.add("active")
    }

    return (
        <>
            <div className='container moreee'>
                <div className='home__container'>
                    <ul className='home__categories'>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon1} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Автомобили</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon2} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Недвижимость</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon3} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Электроника</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon4} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Работа</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon5} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Личные вещи</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon6} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Дом и Сад</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon7} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Стройка и ремонт</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon8} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Хобби и Спорт</p>
                        </li>
                        <li className='home__categories-item'>
                            <div className='home__categories-image-wrapper'>
                                <img src={icon9} alt="icon" />
                            </div>
                            <p className='home__categories-text'>Бизнес и услуги</p>
                        </li>
                    </ul>
                </div>
                <div className='home__products'>
                    <div className='home__links'>
                        <div className='first'>
                            <Link ref={line4} onClick={handleLine4} className="home__link active homeee" to="/">
                                Новые объявления
                                <input id='first' defaultChecked className='home__link-input' type="radio" name='first' />
                                <label htmlFor="first" className='home__link-radio'>
                                </label>
                            </Link>
                        </div>
                        <div className="second">
                            <Link ref={line5} onClick={handleLine5} className="home__link homeee" to="best">
                                <input id='second' className='home__link-input' type="radio" name='first' />
                                Лучщие предложение
                                <label htmlFor="second" className='home__link-radio'>
                                </label>
                            </Link>
                        </div>
                        <span className='home__line'></span>
                    </div>
                    <span className="nav-line"></span>

                    <Routes>
                        <Route path='/*' element={<div className='home__products-wrapper'>
                            <ProductList />
                        </div>} />
                        <Route path='best' element={<h2>Лучщие предложение</h2>} />
                    </Routes>
                </div>
            </div>
                <Slider />
            <div className='container'>
                <div className='watched-wrapper'>
                    <Link ref={line1} onClick={handleLine1} className='home__link liink active' to='/'>
                        <input id='thirth' defaultChecked className='home__link-input' type="radio" name='second' />
                        Просмотренные
                        <label htmlFor="thirth" className='home__link-radio'>
                        </label>
                    </Link>
                    <Link ref={line2} onClick={handleLine2} className='home__link liink' to='choose'>
                        <input id='fourth' className='home__link-input' type="radio" name='second' />
                        Выбор редакции
                        <label htmlFor="fourth" className='home__link-radio'>
                        </label>
                    </Link>
                    <Link ref={line3} onClick={handleLine3} className='home__link liink' to='price'>
                        <input id='fiveth' className='home__link-input' type="radio" name='second' />
                        Выбор редакции
                        <label htmlFor="fiveth" className='home__link-radio'>
                        </label>
                    </Link>
                </div>
                <span className="nav-line"></span>
                <Routes>
                    <Route path='/*' element={<WatchedList />} />
                    <Route path='/choose' element={<h2>Выбор редакции</h2>} />
                    <Route path='/price' element={<h2>Сниженные цены</h2>} />
                </Routes>
            </div>
        </>
    )
}

export default Home