import './auth.scss'
import arrowDown from './../../assets/images/arrow-down.svg'
import authLogo from './../../assets/images/auth-logo.svg'
import { useContext, useEffect, useRef } from 'react'
import { AuthContext } from '../../context/authContext'
import { useNavigate } from 'react-router-dom'


function Auth() {

    let authform = useRef(null)
    const navigate = useNavigate()
    const { token, setToken } = useContext(AuthContext)

    useEffect(() => {
        if (localStorage.getItem("token")) {
            navigate('/')
        }
    }, [])

    function checktoken() {
        if (localStorage.getItem("token")) {
            navigate('/')
        } else {
            alert('Parol yoki login xato')
            authform.current.reset()
        }
    }
    let mail = useRef(null)
    let password = useRef(null)

    function submit(e) {
        e.preventDefault()

        fetch("https://reqres.in/api/login", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(
                {
                    "email": mail.current.value,
                    "password": password.current.value
                }
            )
        })
            .then(req => req.json())
            .then(data => {
                !data.error && localStorage.setItem("token", JSON.stringify(data.token))
                checktoken()
            })
    }
    return (
        <>
            <div className='auth__top'>
                <div className='single__top-wrapper'>
                    <div className='single__top-left'>
                        <select className='single__top-category'>
                            <option value="1"> Любая категория</option>
                        </select>
                        <input className='single__top-search' placeholder='Что будем искать ?' />
                        <select className='single__top-location'>
                            <option value="2">
                                По всей Узбекистану
                            </option>
                        </select>
                        <button className='single__top-btn'>
                            Найти
                        </button>
                    </div>
                    <div className='single__top-right'>
                        <span className='single__top-plus'>+</span>
                        Добавить обьявления
                    </div>
                </div>
            </div>
            <div className='container'>
                <div className='auth__container'>
                    <div className='auth__left'>
                        <h2 className='auth__left-heading'>
                            Ваша регистрация завершена!
                        </h2>
                        <p className='auth__left-success'>
                            Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер
                        </p>
                        <form ref={authform} onSubmit={submit} className='auth__left-form'>
                            <label className='auth__label-name'>
                                Введите email или номер телефона
                                <input ref={mail} className='auth__left-name-input' type="email" placeholder='99891 167 27 23' />
                            </label>
                            <label className='auth__label-password'>
                                Пароль
                                <input ref={password} className='auth__left-password-input' type="password" placeholder='***********' />
                            </label>
                            <button type='submit' className='auth__left-btn'>
                                Войти
                            </button>
                        </form>
                        <div className='auth__or-wrapper'>
                            <span className='auth__or-line'></span>
                            <p className='auth__or-text'>или</p>
                            <span className='auth__or-line'></span>
                        </div>
                        <p className='auth__or-recover'>
                            Восстоновить ваше пароль
                        </p>
                    </div>
                    <div className='auth__right'>
                        <div className='auth__right-wrapper'>
                            <img className='auth__right-logo' src={authLogo} alt="logo" />
                            <p className='auth__right-text'>
                                Откройте для себя
                                целую плошадку
                                для продаже
                            </p>
                            <button className='auth__right-btn'>
                                Как это делать?
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Auth