import { Link } from 'react-router-dom'
import './productCard.scss'

function ProductCard({ id, title, pic, price }) {
    return (
        <li key={id} className='product__item'>
                <div>
                    <img className='product__item-img' src={pic} alt="pic" />
                    <h4 className='product__item-title'>{title}</h4>
                </div>
                <div>
                    <p className='product__item-time'>Вчера 22:55</p>
                    <p className='product__item-price'>{price}$</p>
                </div>
        </li>
    )
}

export default ProductCard