import { Link } from 'react-router-dom'
import slider1 from './../../assets/images/slider1.png'
import slider2 from './../../assets/images/slider2.png'
import slider3 from './../../assets/images/slider3.png'
import slider4 from './../../assets/images/slider4.png'
import './slider.scss'

import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/pagination";


// import required modules
import { Autoplay, FreeMode, Pagination } from "swiper";

export default function App() {
    return (
        <>
            <div className="slider">
                <Swiper
                    slidesPerView={3}
                    spaceBetween={30}
                    freeMode={true}
                    loop={true}
                    autoplay={{
                      delay: 1500,
                      disableOnInteraction: false,
                    }}
                    pagination={{
                        clickable: true,
                        el:".slider-pag"

                    }}
                    modules={[Autoplay , FreeMode, Pagination]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <img className='slider__item' src={slider1} alt="slide" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slider__item' src={slider2} alt="slide" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slider__item' src={slider3} alt="slide" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slider__item' src={slider4} alt="slide" />
                    </SwiperSlide>
                </Swiper>
                <div className='slider-pag'>

                </div>
            </div>
        </>
    );
}
