import './AddProduct.scss'
import cam from './../../assets/images/cam.svg'
import { useFormik } from 'formik'

function AddProduct() {

    const formik = useFormik({
        initialValues: {
            price: '',
            typeMoney:'',
            isOffer:'',
            roomNum:'',
            roomx:'',
            roomY:'',
            isUrban:'',
            roomFloor:'',
            roomHeight:'',
            roomHuge:'',
            homeType:'',
            homeConnection:'',
            isConnection:'',
            telNum:'',
            email:'',
            firstName:''
        },
        onSubmit: values => {
            console.log(values);
        },
    });

    return (
        <>
            <form onSubmit={formik.handleSubmit} className='productWrapper container'>
                <p className='title-label'>Цена</p>
                <div className='prodWStyle'>
                    <div>
                        <input name="price" onChange={formik.handleChange} value={formik.values.price}  className='borderB' type={'text'}></input>
                    </div>

                    <select name="typeMoney" onChange={formik.handleChange} value={formik.values.typeMoney} className='borderB'>
                        <option>Сум</option>
                        <option>$</option>
                        <option>₽</option>
                    </select>

                    <div style={{ marginTop: '0' }} className='product__checkbox-style'>
                        <input name="isOffer" onChange={formik.handleChange} value={formik.values.isOffer} style={{ cursor: "pointer" }} type={'checkbox'}></input>
                        <p className='checkboxText' style={{ margin: '0' }}>Договорная</p>
                    </div>
                </div>

                <p className='title-label'>Количество комнат*</p>
                <div className='prodWStyle'>
                    <div>
                        <input name="roomNum" onChange={formik.handleChange} value={formik.values.roomNum} className='borderB' type={'number'}></input>
                    </div>

                    <input name="roomx" onChange={formik.handleChange} value={formik.values.roomX} className='borderB' type={'number'}></input>

                    <input name="roomY" onChange={formik.handleChange} value={formik.values.roomY} className='borderB' type={'number'}></input>
                </div>

                <p className='title-label'>Расположение*</p>
                <div className='input__container'>
                    <ul className='prodWStyle'>

                        <li className='product__checkbox-style'>
                            <input name="isUrban" onChange={formik.handleChange} value={formik.values.isUrban} style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>В городе</p>
                        </li>

                        <li className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Вдоль трассы</p>
                        </li>

                        <li className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>В дачном массиве</p>
                        </li>
                    </ul>

                    <ul className='prodWStyle'>
                        <li className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>В пригороде</p>
                        </li>

                        <li className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Возле водоема, реки</p>
                        </li>

                        <li className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>На закрытой территории</p>
                        </li>
                    </ul>

                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>В сельской местности</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>В предгорьях</p>
                        </div>
                    </div>
                </div>

                <ul className='prodWStyle'>
                    <li>
                        <p className='title-label'>Этажность дома*</p>
                        <input name="roomFloor" onChange={formik.handleChange} value={formik.values.roomFloor} className='borderB' type={'number'}></input>
                    </li>

                    <li>
                        <p className='title-label'>Высота потолков</p>
                        <input name="roomHeight" onChange={formik.handleChange} value={formik.values.roomHeight} className='borderB' type={'number'}></input>
                    </li>

                    <li>
                        <p className='title-label'>Площадь участка*</p>
                        <input name="roomHuge" onChange={formik.handleChange} value={formik.values.roomHuge} className='borderB' type={'number'}></input>
                    </li>
                </ul>

                <p className='title-label'>Состояние дома</p>
                <div>
                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Авторский проект</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Требует ремонта</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Черновая отделка</p>
                        </div>
                    </div>

                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Средний ремонт</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Предчистовая отделка</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Под снос</p>
                        </div>
                    </div>

                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Евроремонт</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Не достроен</p>
                        </div>
                    </div>
                </div>

                <div className='prodWStyle'>
                    <div>
                        <p name="homeType" onChange={formik.handleChange} value={formik.values.homeType} className='title-label' style={{ marginBottom: '13px' }}>Тип дома</p>

                        <select className='borderB'>
                            <option className='optionSty'>Коттедж</option>
                            <option>ВИЛЛА</option>
                            <option>ВАЙТХАУС</option>
                        </select>
                    </div>

                    <div>
                        <p className='title-label' style={{ marginBottom: '13px' }}>Электричество</p>

                        <select name="homeConnection" onChange={formik.handleChange} value={formik.values.homeConnection} className='borderB'>
                            <option className='optionSty'>Есть возможность подключения</option>
                            <option>ПОДКЛЮЧАЙСЯ</option>
                            <option>ГОУУ</option>
                        </select>
                    </div>
                </div>

                <p className='title-label'>Отопление</p>
                <div>
                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Центральное</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>На жидком топлив</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Без отопления</p>
                        </div>
                    </div>

                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>На газе</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Электрическое</p>
                        </div>
                    </div>

                    <div className='prodWStyle'>
                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>На твердом топливе</p>
                        </div>

                        <div className='product__checkbox-style'>
                            <input style={{ cursor: "pointer" }} type={'checkbox'}></input>
                            <p className='checkboxText'>Смешанное</p>
                        </div>
                    </div>
                </div>

                <div>
                    <p className='title-label'>Электричество</p>

                    <p className='prodTitle'>Есть возможность подключения</p>

                    <textarea name="isConnection" onChange={formik.handleChange} value={formik.values.isConnection} className='borderB' rows={'10'} cols={'85'} type={'text'}></textarea>

                    <p className='title-label' style={{ marginTop: '10px', marginBottom: '14px', color: "rgba(0, 0, 0 , 0.55)", fontWeight: '500' }}>9000 знаков осталос</p>
                </div>

                <div className='fileWrapper'>
                    <input id='cam1' className='input-file' type={'file'}></input>
                    <input id='cam2' className='input-file' type={'file'}></input>
                    <input id='cam3' className='input-file' type={'file'}></input>
                    <input id='cam4' className='input-file' type={'file'}></input>
                    <input id='cam5' className='input-file' type={'file'}></input>
                    <input id='cam6' className='input-file' type={'file'}></input>
                    <input id='cam7' className='input-file' type={'file'}></input>
                    <input id='cam8' className='input-file' type={'file'}></input>
                    <input id='cam9' className='input-file' type={'file'}></input>
                    <input id='cam10' className='input-file' type={'file'}></input>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam1'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam2'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam3'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam4'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam5'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam6'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam7'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam8'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam9'}>
                        <img src={cam} alt='cam' />
                    </label>

                    <label style={{ cursor: "pointer" }} htmlFor={'cam10'}>
                        <img src={cam} alt='cam' />
                    </label>
                </div>

                <div style={{ marginTop: '41px' }}>
                    <h3 className='prodTitle'>Местоположение объекта</h3>

                    <div className='prodWStyle'>
                        <div>
                            <p className='title-label' style={{ marginLeft: '5px', marginBottom: '13px' }}>Регион</p>

                            <select className='borderB regSelect' style={{ marginBottom: '19px' }}>
                                <option className='optionSty'>Ташкент</option>
                                <option>Истамбул</option>
                                <option>Америка</option>
                            </select>
                        </div>

                        <div>

                            <p className='title-label' style={{ marginLeft: '34px', marginBottom: '13px' }}>Город / Район</p>
                            <select className='borderB raySelect' style={{ marginBottom: '19px' }}   >
                                <option className='optionSty'>Мирза Улугбекский район</option>
                                <option>Кибрай</option>
                            </select>
                        </div>
                    </div>
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d191560.54749443315!2d69.16111293814825!3d41.39290587956884!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x38aef2da20187967%3A0x9fb29cb3872aec24!2z0KLQsNGI0LrQtdC90YLRgdC60LjQuSDRgNCw0LnQvtC9LCDQo9C30LHQtdC60LjRgdGC0LDQvQ!5e0!3m2!1sru!2s!4v1665323831582!5m2!1sru!2s"
                        width={700}
                        height={376}
                        frameBorder="0"
                        allowFullScreen=""
                        loading="lazy"
                        referrerPolicy="no-referrer-when-downgrade">
                    </iframe>
                </div>

                <div className='productCon'>
                    <div>
                        <p className='addContact title-label'>Телефонный номер</p>

                        <input name="telNum" onChange={formik.handleChange} value={formik.values.telNum} placeholder='+99894 888 74452' className='borderB addInputC' type={'number'}></input>
                    </div>

                    <div>
                        <p className='addContact title-label'>Email-адрес</p>

                        <input name="email" onChange={formik.handleChange} value={formik.values.email} className='borderB addInputC' type={'email'}></input>
                    </div>

                    <div>
                        <p className='addContact title-label'>Контактный лицо</p>

                        <input name="firstName" onChange={formik.handleChange} value={formik.values.firstName} placeholder='Зухриддин' className='borderB addInputC' type={'text'}></input>
                    </div>
                </div>

                <div className='prodWStyle'>
                    <button type='submit' className='addBtn prodTitle'>Добавить обьявления бесплатно</button>

                    <p style={{ cursor: "pointer" }} className='link prodTitle'>Предпросмотр</p>
                </div>
            </form>
        </>
    )
}

export default AddProduct