import './sort.scss'
import findImg from './../../assets/images/findimg.svg'
import locate from './../../assets/images/locate.svg'
import cross from './../../assets/images/rubricCross.svg'
import arrowDown from './../../assets/images/rubricArrow.svg'
import Downcondition from './../../assets/images/arrow-donw-condition.svg'
import topped from './../../assets/images/topped.svg'
import { Link } from 'react-router-dom'
import ProductCard from '../../components/productCard'
import { useRef, useState } from 'react'

function Sort() {
	let moreWrapper = useRef(null)
	let fullWrapper = useRef(null)
	let shortWrapper = useRef(null)
	let [product, setProduct] = useState([])
	fetch('https://fakestoreapi.com/products')
		.then(res => res.json())
		.then(data => setProduct(data))
	function handleShow() {
		fullWrapper.current.style.display = "flex"
		moreWrapper.current.remove()
	}
	return (
		<>
			<div className='sort__container container'>
				<div className='sort__input-wrapper'>
					<img className='sort__input-img' src={findImg} alt="loop" />
					<p className='sort__input-text'>
						Введите название обявления или  <span className='sort__input-link'>выберите из списка</span>
					</p>
				</div>
				<div className='sort__type'>
					<ul className='sort__type-list'>
						<li className='sort__type-item'>
							Интерсное
						</li>
						<li className='sort__type-item'>
							Топ
						</li>
						<li className='sort__type-item'>
							Отличные предложение
						</li>
						<li className='sort__type-item'>
							Супер цена
						</li>
						<li className='sort__type-item'>
							г.Ташкент <img src={locate} alt="locate" />
						</li>
						<li className='sort__type-item'>
							г.Термез <img src={locate} alt="locate" />
						</li>
						<li className='sort__type-item'>
							г.Самарканд <img src={locate} alt="locate" />
						</li>
					</ul>
				</div>
				<div className='sort__rubric'>
					<div className='sort__rubric-now-wrapper'>
						<p className='sort__rubric-head'>
							Вы сейчас в рубрике
						</p>
						<div className='sort__rubric-wrapper'>
							<div className='sort__rubric-item'>
								<p className='sort__rubric-name'>
									Электроника
								</p>
								<img className='sort__rubric-cross' src={cross} alt="cross" />
							</div>
							<div className='sort__rubric-item'>
								<p className='sort__rubric-name'>
									Мониторы
								</p>
								<img className='sort__rubric-cross' src={cross} alt="cross" />
							</div>
							<img className='sort__rubric-arrow' src={arrowDown} alt="arrow" />
						</div>
					</div>
					<div className='sort__rubric-slider-wrapper'>
						<p className='sort__rubric-slider-name'>
							Выберите цену от и до
						</p>
						<input type="range" className='sort__rubric-slider' />
					</div>
					<div className='sort__rubric-sorting'>
						<div className='sort__rubric-condition'>
							<p className='sort__rubric-condition-title'>
								Состояние
							</p>
							<img className='sort__rubric-arrow' src={Downcondition} alt="arrow" />
						</div>
						<div className='sort__rubric-type'>
							<p className='sort__rubric-type-title'>
								Тип объявщика
							</p>
							<img className='sort__rubric-arrow' src={Downcondition} alt="arrow" />
						</div>
						<div className='sort__rubric-location'>
							<p className='sort__rubric-location-title'>
								Ташкент &gt; Мирза улугбекский район
							</p>
							<img className='sort__rubric-arrow' src={Downcondition} alt="arrow" />
						</div>
						<button className='sort__rubric-btn'>Применить</button>
					</div>
				</div>
			</div>
			<span className='sort__line'></span>
			<div className='sort__products-list container'>
				<ul>
					<div className='product-list product__top'>
						{
							product?.slice(0, 4).map(item => (
								<Link key={item.id} className="topped__item" style={{ textDecoration: "none" }} to={`/product/${item.id}`}>
									<ProductCard
										key={item.id}
										pic={item.image}
										title={item.title}
										price={item.price}
									/>
									<img src={topped} className='topped'></img>
								</Link>
							))
						}
					</div>
					<div className='product-list' ref={shortWrapper}>
						{
							product?.map(item => (
								<Link key={item.id} style={{ textDecoration: "none" }} to={`/product/${item.id}`}>
									<ProductCard
										key={item.id}
										pic={item.image}
										title={item.title}
										price={item.price}
									/>
								</Link>
							))
						}
					</div>
					<div ref={fullWrapper} style={{ display: "none" }} className='product-list'>
						{
							product?.map(item => (
								<Link key={item.id} style={{ textDecoration: "none" }} to={`/product/${item.id}`}>
									<ProductCard
										key={item.id}
										pic={item.image}
										title={item.title}
										price={item.price}
									/>
								</Link>
							))
						}
					</div>
				</ul>
				<div className='more__button wrapper' ref={moreWrapper}>
					<span className='product__line'></span>
					<p className='more__button-text' onClick={handleShow}>Покозать еще 20</p>
				</div>
				<div className='sort__now-searching'>
					<img className='sort__now-searching-img' src={findImg} alt="find" />
					<h3 className='sort__now-searching-heading-top'>
						СЕЙЧАС ИЩУТ
					</h3>
				</div>
				<div className='sort__now-searching-list'>
					<div className='sort__now-searching-item'>
						<p className='sort__now-searching-heading'>
							купить Мясная продукция
						</p>
						<p className='sort__now-searching-text'>
							<img className='sort__now-searching-item-img' src={findImg} alt="find" />
							2 часа назад
						</p>
					</div>
					<div className='sort__now-searching-item'>
						<p className='sort__now-searching-heading'>
							купить Мясная продукция
						</p>
						<p className='sort__now-searching-text'>
							<img className='sort__now-searching-item-img' src={findImg} alt="find" />
							2 часа назад
						</p>
					</div>
					<div className='sort__now-searching-item'>
						<p className='sort__now-searching-heading'>
							купить Мясная продукция
						</p>
						<p className='sort__now-searching-text'>
							<img className='sort__now-searching-item-img' src={findImg} alt="find" />
							2 часа назад
						</p>
					</div>
					<div className='sort__now-searching-item'>
						<p className='sort__now-searching-heading'>
							купить Мясная продукция
						</p>
						<p className='sort__now-searching-text'>
							<img className='sort__now-searching-item-img' src={findImg} alt="find" />
							2 часа назад
						</p>
					</div>

					<div className='sort__now-searching-item'>
						<p className='sort__now-searching-heading'>
							купить Мясная продукция
						</p>
						<p className='sort__now-searching-text'>
							<img className='sort__now-searching-item-img' src={findImg} alt="find" />
							2 часа назад
						</p>
					</div>
				</div>
				<div className='sort__interesting'>
					<h3 className='sort__interesting-heading'>
						САМОЕ ИНТЕРЕСНОЕ
					</h3>
					<ul className='sort__interesting-list'>
						{
							product?.slice(0, 3).map(item => (
								<Link className='sort__interesting-item' key={item.id} style={{ textDecoration: "none" }} to={`/product/${item.id}`}>
									<ProductCard
										key={item.id}
										pic={item.image}
										title={item.title}
										price={item.price}
									/>
								</Link>
							))
						}
					</ul>
				</div>

			</div>
		</>
	)
}

export default Sort